.main-popup {
    --popup-spacer: var(--container-padding);
    --popup-max-width: 700px;
    position: fixed;
    inset: 0;
    z-index: 2000;
    background-color: var(--color-curtain);
    overflow-y: auto;
    overscroll-behavior: contain;
    opacity: 0;
    visibility: hidden;
    transition:
        opacity var(--transition),
        visibility var(--transition);
    &.active {
        opacity: 1;
        visibility: visible;
    }

    &__dialog {
        outline: none;
        padding: var(--popup-spacer);
    }
    &__main {
        background-color: var(--color-white);
        position: relative;
        z-index: 1;
        margin: 0 auto;
        max-width: var(--popup-max-width);
    }
    &__title-wrap {
        padding: var(--popup-spacer) 0 0 var(--popup-spacer);
    }
    &__body {
        padding: 0 var(--popup-spacer) var(--popup-spacer);
        :first-child {
            margin-top: 0;
        }
        :last-child {
            margin-bottom: 0;
        }
    }
}
